function StateInput() {
    // Need to learn more about state
    // two variable to reset and update value
    const [firstName, setName] = useState('');
    const newText = (event) => {
        setName(event.target.value);
    }

    return (
        <React.Fragment>
            <input placeholder="type anythings" type="text" name="firstName" onChange={newText} />
            <p>{firstName}</p>
        </React.Fragment>
    )

}



export default StateInput;