import React from 'react';
import {
    Link
} from "react-router-dom";
import './Navbar.css';
// import "./Navbar-m.js"


export default function Navbar() {
    return (
        < section className="navigation" id="navigation" >
            <div className="nav-container">
                <div className="nav-r">
                    <Link to="/">LOGO</Link>
                </div>
                <nav className="nav-l">
                    <div className="nav-mobile"><a id="nav-toggle" href="#!"><span></span></a></div>

                    <ul className="nav-list">
                        <li><Link to="/">Joke</Link></li>
                        <li><Link to="/joke_manual">Joke manual</Link></li>
                        <li><Link to="/about">About</Link></li>
                        <li><Link to="/todo">Todo App</Link></li>
                        <li><Link to="/slider">Slide show</Link></li>
                        <li><Link to="/nestlink">Nestlink</Link></li>
                        <li><Link to="/404">error page</Link></li>  {/* // "fallback" route, to catch 404 errors. */}
                    </ul>
                </nav>
            </div>
            {/* <script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.4.1/jquery.min.js"
                integrity="sha256-CSXorXvZcTkaix6Yvo6HppcZGetbYMGWSFlBw8HfCJo=" crossorigin="anonymous"></script>
            <script src="{Navbar-m}"></script> */}
        </section >
    )
}