import React, { useState } from 'react';
import './Joke.css'

export default function Joke() {

    // Simple data binding method    
    const initialJoke = {
        joke: "haha funny joke by hard coding"
    };
    const [joking] = useState(initialJoke);

    return (
        <div className="container_joke">
        <div className="card">
            <div className="card-t">
                <h1>It's a joke.</h1>
            </div>
            <p>simple joke: {joking.joke}</p>

        </div>
    </div>
    )
}