import React, { useState } from 'react';
import './Slider.css'

import ImgComp from "./ImgComp"
import i1 from "./pics/1.png"
import i2 from "./pics/2.png"
import i3 from "./pics/3.png"
import i4 from "./pics/4.png"
import i5 from "./pics/5.png"

import { faChevronRight } from '@fortawesome/free-solid-svg-icons';
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export default function Slider() {
    let slideArr = [<ImgComp src={i1} />,
    <ImgComp src={i2} />,
    <ImgComp src={i3} />,
    <ImgComp src={i4} />,
    <ImgComp src={i5} />];


    const [x, setX] = useState(0) //initial state
    let _style = { transform: 'translateX(' + x + 'vw)' };
    const goLeft = () => {
        console.log(x);
        if (x === 0) {
            setX(0)
        }
        else {
            setX(x + 100);
        }
    };
    const goRight = () => {
        console.log(x);
        if (x === -100 * (slideArr.length - 1)) {
            setX(0)
        }
        else {
            setX(x - 100);
        }
    };

    return (
        <div className="slider">
            {
                slideArr.map((item, index) => {
                    return (
                        <div key={index} className="slide" style={_style}>
                            {item}
                        </div>
                    )
                })
            }
            <button id="goLeft" onClick={goLeft} className="slide_btn">
                <span>
                    <FontAwesomeIcon className="faicons"
                        icon={faChevronLeft} />
                </span>
            </button>

            <button id="goRight" onClick={goRight} className="slide_btn">
                <span>
                    <FontAwesomeIcon className="faicons"
                        icon={faChevronRight} />
                </span>
            </button>
        </div>
    )

}