import React from 'react';
import './ListItems.css';

// import lib using npm
// import { library } from '@fortawesome/fontawesome-svg-core';
import { faTrash } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import FlipMove from 'react-flip-move';

export default function ListItems(props) {

    const items = props.items; // set of array
    const listItems = items.map(item => {
        return <div className="list" key={item.key}>
            <p>
                <input type="text" id={item.key} value={item.text} onChange={
                    (e) => {
                        props.setUpdate(e.target.value, item.key)
                    }} />
                <span>
                    <FontAwesomeIcon className="faicons"
                        icon={faTrash} onClick={() => props.deleteItem(item.key)} />
                    {/* delete with specific key that need to order in ListItems */}

                </span>
            </p>

        </div>
    })

    return (
        <div>
            <small>From ListItems.js</small>
            <div className="grouplist">
                <FlipMove duration={200} easing="ease-in-out">
                    {listItems}
                </FlipMove>
            </div>
        </div>
    )
}
