import React from 'react';
import ListItems from './ListItems';
import './Todo.css'

// Function using in this app
// Add Item
//// set default state with constructor
//// Entering text: handleInput() (update with on change)
//// Click add: addItem() (Add item in list)

class Todo extends React.Component {
    constructor(props) { //set template
        super(props);
        this.state = { // start with empty
            items: [],
            currentItem: {
                text: '',
                key: ''
            }
        }
        // make function bind with this!
        this.handleInput = this.handleInput.bind(this);
        this.addItem = this.addItem.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
        this.setUpdate = this.setUpdate.bind(this);
    }

    handleInput(e) {
        this.setState({
            //setState with value
            currentItem: {
                text: e.target.value,
                key: Date.now()
            }
        })
    }

    addItem(e) {
        e.preventDefault(); // prevent form post refresh
        const newItem = this.state.currentItem;
        console.log(newItem); //check item has been catch

        //validation
        if (newItem.text !== "") {
            const newItems = [...this.state.items, newItem];
            this.setState({
                //reset value with setState
                items: newItems,
                currentItem: {
                    text: '',
                    key: ''
                }
            })
        }
    }

    // Delete Item
    //// 
    deleteItem(key) {
        const filteredItems = this.state.items.filter(item => item.key!==key)
        // show only items that's not keyID

        // update state
        this.setState({
            items:filteredItems
        })
    }

    // Edit Item
    ////
    setUpdate(text, key) {
        const items = this.state.items;
        // need to loop everytime because we didnot have sorting id key
        items.map((item) => {
            if(item.key === key) {
               item.text = text; 
            }
            return item;
        })
        this.setState({
             items: items
        }) 
    }

    render() {
        return (
            <div className="todo_container">
                <header>
                    <h1>TODOLIST APP</h1>
                    <form id="to-do-form" onSubmit={this.addItem}>
                        <div className="todo_add">
                            <input type="text" placeholder="Enter text"
                                value={this.state.currentItem.text} onChange={this.handleInput} />
                            <button className="button_todo" type="submit">Add</button>
                        </div>

                    </form>
                </header>

                {/* pass variable through prop */}
                <ListItems items={this.state.items}
                deleteItem={this.deleteItem}
                setUpdate={this.setUpdate}></ListItems>
            </div>
        );
    }
}

export default Todo;